<script setup>
import { ref, computed } from 'vue'

const questions = ref([
  {
    question: 'What is the primary purpose of Vue.js in web development?',
    answer: 2,
    options: [
      'To serve as a back-end server',
      'To handle database operations',
      'To manage state and build user interfaces'
    ],
    selected: null
  },

  {
    question: 'Which directive in Vue.js is used for two-way data binding?',
    answer: 1,
    options: [
      'v-show',
      'v-model',
      'v-bind'
    ],
    selected: null
  },

  {
    question: 'What does the "v-for" directive in Vue.js do?',
    answer: 0,
    options: [
      'Loops through an array to render elements',
      'Adds an event listener',
      'Defines a computed property'
    ],
    selected: null
  },

  {
    question: 'Vue.js, what is the purpose of the "computed" property?',
    answer: 0,
    options: [
      'It computes derived state based on reactive dependencies',
      'It sets up routes for navigation',
      'It defines methods to handle user input'
    ],
    selected: null
  },

  {
    question: 'What does the "v-if" directive in Vue.js do?',
    answer: 1,
    options: [
      'Adds an event listener',
      'Conditionally renders an element',
      'Defines a computed property'
    ],
    selected: null
  },

  {
    question: 'In Vue.js, what is the purpose of the "v-bind" directive?',
    answer: 1,
    options: [
      'Adds an event listener',
      'Binds an attribute or property to an expression',
      'Sets up routes for navigation'
    ],
    selected: null
  },

  {
    question: 'What is the role of the "watch" option in a Vue component?',
    answer: 1,
    options: [
      'Handles form submissions',
      'Observes changes to data properties and performs actions',
      'Defines methods to handle user input'
    ],
    selected: null
  },

  {
    question: 'How can you communicate between parent and child components in Vue.js?',
    answer: 1,
    options: [
      'Using the v-if directive',
      'Through the use of props and custom events',
      'Defining a computed property'
    ],
    selected: null
  },

  {
    question: 'What is the purpose of the Vue Router library in Vue.js?',
    answer: 2,
    options: [
      'Manages global state in the application',
      'Handles HTTP requests',
      'Enables client-side routing in a single-page application'
    ],
    selected: null
  },

  {
    question: 'How do you handle user input in Vue.js forms?',
    answer: 1,
    options: [
      'Using the v-show directive',
      'Through the use of methods and data binding',
      'Defining a computed property'
    ],
    selected: null
  },

  {
    question: 'What is the significance of the "created" lifecycle hook in Vue.js?',
    answer: 1,
    options: [
      'Triggered after the component is added to the DOM',
      'Executes when the component is created but before any DOM is rendered',
      'Invoked when the component is updated'
    ],
    selected: null
  },

  {
    question: 'How do you conditionally apply styles in Vue.js?',
    answer: 1,
    options: [
      'Using the v-bind directive',
      'Through the use of computed properties',
      'Adding inline styles directly in the template'
    ],
    selected: null
  },

  {
    question: 'What is the purpose of the "provide" and "inject" options in Vue.js?',
    answer: 2,
    options: [
      'Sets up routes for navigation',
      'Enables communication between sibling components',
      'Facilitates dependency injection in deeply nested components'
    ],
    selected: null
  },

  {
    question: 'Which Vue.js feature allows you to dynamically switch between components?',
    answer: 2,
    options: [
      'The v-show directive',
      'The v-for directive',
      'The <component> element with dynamic components'
    ],
    selected: null
  },

  {
    question: 'Which lifecycle hook in Vue.js is triggered after the component has been added to the DOM?',
    answer: 2,
    options: [
      'beforeCreate',
      'updated',
      'mounted'
    ],
    selected: null
  }
])

const quizIsDone = ref(false)

const currentQuestion = ref(0)

const score = computed(() => {
  let value = 0
  questions.value.forEach(question => {
    if (question.selected == question.answer) {
      value++
    }
  })

  return value
})

const getCurrentQuestion = computed(() => {
  let question = questions.value[currentQuestion.value]
  question.index = currentQuestion.value
  return question
})

const answerSet = (a) => {
  questions.value[currentQuestion.value].selected = a.target.value
  a.target.value = null
}

const nextQuestion = () => {
  if (currentQuestion.value < questions.value.length - 1) {
    currentQuestion.value++
  } else {
      quizIsDone.value = true
  }
}
</script>

<template>
  <main class="app">
    <h1>My VUE.js quiz</h1>

    <section class="quiz" v-if="!quizIsDone">
      <div class="quiz-info">
        <span class="question">
          {{ getCurrentQuestion.question }}
        </span>

        <span class="score">
          Score {{ score }} / {{ questions.length }}
        </span>
      </div>

      <div class="options">
        <label 
          v-for="(option, index) in getCurrentQuestion.options"
          :key="index"
          :class="`option ${
            getCurrentQuestion.selected == index
              ? index == getCurrentQuestion.answer
                ? 'correct'
                : 'incorrect'
              : ''
          } ${
            getCurrentQuestion.selected != null
            && index != getCurrentQuestion.selected
              ? 'disabled'
              : ''
          }`">

          <input type="radio"
            :name="getCurrentQuestion.index"
            :value="index"
            v-model="getCurrentQuestion.selected"
            :disabled="getCurrentQuestion.selected"
            @change="answerSet"
          >

            <span>
              {{ option }}
            </span>
        </label>
      </div>

      <div class="lower-info">

        <button
          @click="nextQuestion"
          :disabled="!getCurrentQuestion.selected">
          {{ 
            getCurrentQuestion.index == questions.length -1
              ? 'Check for results'
              : getCurrentQuestion.selected == null
                  ? 'Select an option'
                  : 'Next question'
          }}
        </button>
        <span class="progress">
          Question {{ currentQuestion + 1 }} / {{ questions.length }}
        </span>
      </div>
    </section>

    <section v-else>
      <h2 class="congrats">You have finished my VUE.js quiz!</h2>
      <p class="score">Your score is {{ score }} / {{ questions.length }}</p>
    </section>
  </main>
</template>

